import * as shopAPIs from "../apis/shopAPIs";

const SET_TEST_REDUX = "SET_TEST_REDUX";

export const setTestRedux = (test: boolean) => (dispatch: any) => {
    dispatch({ type: SET_TEST_REDUX, test });
};

const initialState: any = {
    test: false
};

export default function CommonReducer(state = initialState, action: any) {
    switch (action.type) {
        case SET_TEST_REDUX:
            return {
                ...state,
                test: action.test
            };
        default:
            return state;
    }
}
