import React, { useState } from 'react';
import { Table, Input, InputNumber, Form, Image } from 'antd';
import PageHeaderComp from '../pageHeader';
import fakeDataProduct from '../../../fakeData/products';

interface Item {
  key: string;
  name: string;
  price: number;
  active: number;
  count: number;
  image: string;
  voucherId: string;
  categoryId: string;
  description: string;
}
interface EditableCellProps extends React.HTMLAttributes<HTMLElement> {
  editing: boolean;
  dataIndex: string;
  title: any;
  inputType: 'number' | 'text';
  record: Item;
  index: number;
  children: React.ReactNode;
}

const EditableCell: React.FC<EditableCellProps> = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'number' ? <InputNumber /> : <Input />;

  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{ margin: 0 }}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const FeedBack = () => {
  const [form] = Form.useForm();
  const [data, setData] = useState(
    fakeDataProduct.map((item) => {
      return {
        key: item.key.toString(),
        name: item.name,
        price: parseFloat(item.price),
        active: item.active,
        count: item.count,
        image: item.image,
        voucherId: item.voucherId,
        categoryId: item.categoryId,
        description: item.description,
      };
    })
  );

  const columns = [
    {
      title: 'name',
      dataIndex: 'name',
      width: '10%',
    },
    {
      title: 'image',
      dataIndex: 'image',
      width: '10%',
      render: (_: any, record: Item) => {
        return (
          <Image
            // width={200}
            src={record.image}
          />
        );
      },
    },
    {
      title: 'price',
      dataIndex: 'price',
      width: '10%',
    },
    {
      title: 'active',
      dataIndex: 'active',
      width: '10%',
    },
    {
      title: 'count',
      dataIndex: 'count',
      width: '10%',
    },
    {
      title: 'voucherId',
      dataIndex: 'voucherId',
      width: '10%',
    },
    {
      title: 'categoryId',
      dataIndex: 'categoryId',
      width: '10%',
    },
    {
      title: 'description',
      dataIndex: 'description',
      width: '15%',
    },
  ];

  const title = 'FeedBack';
  return (
    <>
      <PageHeaderComp title={title}></PageHeaderComp>
      <Form form={form} component={false}>
        <Table
          components={{
            body: {
              cell: EditableCell,
            },
          }}
          bordered
          dataSource={data}
          columns={columns}
          rowClassName='editable-row'
        />
      </Form>
    </>
  );
};

export default FeedBack;
