import { Button, Menu } from 'antd';
import {
  AppstoreOutlined,
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  PieChartOutlined,
  DesktopOutlined,
  ContainerOutlined,
  MailOutlined,
  AppstoreAddOutlined,
  WechatOutlined,
  ShoppingCartOutlined,
  IdcardOutlined,
  CodeSandboxOutlined,
  LoginOutlined,
} from '@ant-design/icons';
import React, { useState } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { Layout } from 'antd';
import { ROUTERS } from '../../../router/ROUTERS';

const { SubMenu } = Menu;

interface props {
  collapsed: boolean;
  toggleCollapsed: () => void;
}

const MenuNav = (props: any) => {
  const history = useHistory();
  const location = useLocation();

  const [collapsed, setCollapsed] = useState(false);

  const toggleCollapsed = () => {
    setCollapsed(!collapsed);
  };

  return (
    <Layout>
      <div
        style={{
          overflow: 'auto',
          height: '100%',
          position: 'fixed',
          left: 0,
          width: props.collapsed ? 48 : 256,
          background: '#001529',
        }}
      >
        <Button
          type='primary'
          onClick={props.toggleCollapsed}
          style={{ marginBottom: 16 }}
        >
          {React.createElement(
            props.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined
          )}
        </Button>
        <Menu
          defaultSelectedKeys={
            location.pathname === ROUTERS.ADMIN
              ? ['1']
              : location.pathname === ROUTERS.PRODUCTS
              ? ['2']
              : location.pathname === ROUTERS.USERS
              ? ['3']
              : location.pathname === ROUTERS.ORDERS
              ? ['4']
              : location.pathname === ROUTERS.CATEGORY
              ? ['5']
              : location.pathname === ROUTERS.VOUCHER
              ? ['6']
              : location.pathname === ROUTERS.FEEDBACK
              ? ['7']
              : ['1']
          }
          defaultOpenKeys={['sub1']}
          mode='inline'
          theme='dark'
          inlineCollapsed={collapsed}
        >
          <Menu.Item
            key='1'
            icon={<PieChartOutlined />}
            onClick={() => history.push(ROUTERS.ADMIN)}
          >
            Dashboard
          </Menu.Item>
          <Menu.Item
            key='2'
            icon={<CodeSandboxOutlined />}
            onClick={() => history.push(ROUTERS.PRODUCTS)}
          >
            Products
          </Menu.Item>
          <Menu.Item
            key='3'
            icon={<IdcardOutlined />}
            onClick={() => history.push(ROUTERS.USERS)}
          >
            Users
          </Menu.Item>
          <Menu.Item
            key='4'
            icon={<ShoppingCartOutlined />}
            onClick={() => history.push(ROUTERS.ORDERS)}
          >
            Orders
          </Menu.Item>
          <Menu.Item
            key='5'
            icon={<AppstoreOutlined />}
            onClick={() => history.push(ROUTERS.CATEGORY)}
          >
            Category
          </Menu.Item>
          <Menu.Item
            key='6'
            icon={<DesktopOutlined />}
            onClick={() => history.push(ROUTERS.VOUCHER)}
          >
            Voucher
          </Menu.Item>
          <Menu.Item
            key='7'
            icon={<WechatOutlined />}
            onClick={() => history.push(ROUTERS.FEEDBACK)}
          >
            Feedback
          </Menu.Item>
          <Menu.Item
            key='8'
            icon={<LoginOutlined />}
            onClick={() => {
              localStorage.removeItem('token');
              history.push(ROUTERS.HOME);
            }}
          >
            LogOut
          </Menu.Item>
        </Menu>
      </div>
    </Layout>
  );
};

export default MenuNav;
