import React from "react";

import { BrowserRouter } from "react-router-dom";

import PrivateRoute from "../components/PrivateRoute";
import { PublicRoute } from "../components/PublicRoute";
import Admin from "../pages/admin/admin";
import { Login } from "../pages/auth/login";
import { Home } from "../pages/home/home";
import { ROUTERS } from "./ROUTERS";

export const Shops = () => {
    return (
        <BrowserRouter>
            <PrivateRoute exact path={ROUTERS.HOME} component={Home} />
            <PublicRoute exact path={ROUTERS.ADMIN} component={Admin} />
            <PublicRoute exact path={ROUTERS.PRODUCTS} component={Admin} />
            <PublicRoute exact path={ROUTERS.USERS} component={Admin} />
            <PublicRoute exact path={ROUTERS.ORDERS} component={Admin} />
            <PublicRoute exact path={ROUTERS.CATEGORY} component={Admin} />
            <PublicRoute exact path={ROUTERS.VOUCHER} component={Admin} />
            <PublicRoute exact path={ROUTERS.FEEDBACK} component={Admin} />
            <PublicRoute exact path={ROUTERS.GENERALBOOK} component={Admin} />
            <PublicRoute exact path={ROUTERS.ADDBOOK} component={Admin} />
            <PublicRoute exact path={ROUTERS.LOGIN} component={Login} />
        </BrowserRouter>
    );
};
