import { Drawer, Form, Button, Col, Row, Input, Select, Typography } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import React, { useState } from 'react';
import UploadImage from '../upload/UploadImage';
import UploadImageUpdate from '../upload/UploadImageUpdate';

const { Option } = Select;

const layout = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
};


const UsersFormUpdate = (props: any) => {

    const { record } = props;
    const { name, price, active, count, image, voucherId, categoryId, description } = record;

    const { Title } = Typography;

    const [visible, setVisible] = useState(false);

    const showDrawer = () => {
        setVisible(true);
    };

    const onClose = () => {
        form.resetFields();
        setVisible(false);
    };

    const [form] = Form.useForm();

    const onFinish = (value: any) => {
        console.log(value, "value");

    };

    const onFinishFailed = (errorInfo: any) => {
        console.log(errorInfo);
    };

    return (
        <>
            <a onClick={showDrawer}> Edit</a>
            <Drawer
                title="Update a User"
                width={720}
                onClose={onClose}
                visible={visible}
                bodyStyle={{ paddingBottom: 80 }}
                footer={
                    <div
                        style={{
                            textAlign: 'right',
                        }}
                    >
                        <Button onClick={onClose} style={{ marginRight: 8 }}>
                            Cancel
              </Button>
                        <Button
                            form='update-product'
                            key='submit'
                            htmlType='submit'
                            type="primary">
                            Submit
              </Button>
                    </div>
                }
            >
                <Form
                    layout="vertical"
                    name='basic'
                    id='update-product'
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                    className='em_form'
                    form={form}
                    // {...layout}
                    initialValues={{
                        name: name ? name : '',
                        price: price ? price : '',
                        active: active ? active : '',
                        count: count ? count : '',
                        image: image ? image : '',
                        voucherId: voucherId ? voucherId : '',
                        categoryId: categoryId ? categoryId : '',
                        description: description ? description : '',
                    }}
                >
                    <Row gutter={16} style={{ paddingBottom: 5 }}>
                        <Col >
                            <Title level={5}>Upload Image</Title>
                            <UploadImageUpdate image={image} />
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col span={12}>
                            <Form.Item
                                name="name"
                                label="Name"
                                rules={[{ required: true, message: 'Please enter user name' }]}
                            >
                                <Input placeholder="Please enter user name" />
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item
                                name="price"
                                label="Price"
                                rules={[{ required: true, message: 'Please enter price' }]}
                            >
                                <Input placeholder="Please enter price" />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col span={12}>
                            <Form.Item
                                name="count"
                                label="Count"
                                rules={[{ required: true, message: 'Please enter count' }]}
                            >
                                <Input placeholder="Please enter count" />
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item
                                name="active"
                                label="Active"
                                rules={[{ required: true, message: 'Please choose the active' }]}
                            >
                                <Select placeholder="Please choose the active" defaultValue={active}>
                                    <Option value="private">Private</Option>
                                    <Option value="public">Public</Option>
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col span={12}>
                            <Form.Item
                                name="voucher"
                                label="Voucher"
                                rules={[{ required: true, message: 'Please choose the voucher' }]}
                            >
                                <Select placeholder="Please choose the voucher" defaultValue={voucherId}>
                                    <Option value="Voucher">Voucher</Option>
                                    <Option value="Voucher1">Voucher1</Option>
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item
                                name="category"
                                label="Category"
                                rules={[{ required: true, message: 'Please choose the category' }]}
                            >
                                <Select placeholder="Please choose the category" defaultValue={categoryId}>
                                    <Option value="category">Category</Option>
                                    <Option value="category1">Category1</Option>
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col span={24}>
                            <Form.Item
                                name="description"
                                label="Description"
                                rules={[
                                    {
                                        required: true,
                                        message: 'please enter url description',
                                    },
                                ]}
                            >
                                <Input.TextArea rows={4} placeholder="please enter url description" />
                            </Form.Item>
                        </Col>
                    </Row>
                </Form>
            </Drawer>
        </>
    );
}


export default UsersFormUpdate;