import React, { useEffect, useState } from 'react';
import {
  Table,
  Input,
  InputNumber,
  Popconfirm,
  Form,
  Button,
  Image,
  message,
} from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import PageHeaderComp from '../pageHeader';
import ProductsForm from './productsForm';
import ProductsFormUpdate from './productsFormUpdate';
import fakeDataProduct from '../../../fakeData/products';
import { DeleteProduct, getProducts } from '../../../apis/shopAPIs';
import { render } from '@testing-library/react';
import openNotification, {
  typeNotification,
} from '../../../components/openNotification';

interface Item {
  key: string;
  name: string;
  price: number;
  active: boolean;
  count: number;
  imageUrl: string;
  voucherId: string;
  categoryId: string;
  describe: string;
}
interface EditableCellProps extends React.HTMLAttributes<HTMLElement> {
  editing: boolean;
  dataIndex: string;
  title: any;
  inputType: 'number' | 'text';
  record: Item;
  index: number;
  children: React.ReactNode;
}

const EditableCell: React.FC<EditableCellProps> = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'number' ? <InputNumber /> : <Input />;

  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{ margin: 0 }}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const Products = () => {
  const [form] = Form.useForm();
  const token: any = localStorage.getItem('token');

  const [data, setData] = useState([]);

  useEffect(() => {
    getProducts().then((data: any) => {
      setData(data?.products || []);
    });
  }, []);

  const [editingKey, setEditingKey] = useState('');

  const isEditing = (record: Item) => record.key === editingKey;

  const cancel = () => {
    setEditingKey('');
  };

  function confirmDelete(record: any) {
    DeleteProduct([`${record._id}`])
      .then(() => {
        getProducts().then((data: any) => {
          setData(data?.products || []);
        });
        openNotification(typeNotification.success, 'delete product success');
      })
      .catch((err) => {
        openNotification(typeNotification.error, 'delete product error');
      });
  }

  function cancelDelete(e: any) {}

  const refetch = () => {
    getProducts().then((data: any) => {
      setData(data?.products || []);
    });
  };

  const columns = [
    {
      title: 'name',
      dataIndex: 'name',
      width: '10%',
    },
    {
      title: 'image',
      dataIndex: 'imageUrl',
      width: '10%',
      render: (_: any, record: Item) => {
        return (
          <Image
            // width={200}
            src={record.imageUrl}
          />
        );
      },
    },
    {
      title: 'price',
      dataIndex: 'price',
      width: '10%',
    },
    {
      title: 'active',
      dataIndex: 'active',
      width: '10%',
      render: (_: any, record: Item) => {
        return <p>{record ? 'Public' : 'Private'}</p>;
      },
    },
    {
      title: 'count',
      dataIndex: 'count',
      width: '10%',
    },
    {
      title: 'voucherId',
      dataIndex: 'voucherId',
      width: '10%',
    },
    {
      title: 'categoryId',
      dataIndex: 'categoryId',
      width: '10%',
    },
    {
      title: 'describe',
      dataIndex: 'describe',
      width: '15%',
    },
    {
      title: 'operation',
      dataIndex: 'operation',
      render: (_: any, record: Item) => {
        return (
          <div>
            {console.log(record, 'record')}
            <ProductsFormUpdate record={record} refetch={refetch} />
            <Popconfirm
              title='Are you sure delete this task?'
              onConfirm={() => confirmDelete(record)}
              onCancel={cancelDelete}
              okText='Yes'
              cancelText='No'
            >
              <a href='#' style={{ color: 'red', paddingLeft: '10px' }}>
                Delete
              </a>
            </Popconfirm>
          </div>
        );
      },
    },
  ];

  const title = 'Products';
  return (
    <>
      <PageHeaderComp title={title}></PageHeaderComp>
      <div>
        <div
          className='add-book-btn'
          style={{
            margin: '20px auto',
            textAlign: 'right',
          }}
        >
          <ProductsForm refetch={refetch} />
        </div>

        <Form form={form} component={false}>
          <Table
            components={{
              body: {
                cell: EditableCell,
              },
            }}
            bordered
            dataSource={data}
            columns={columns}
            rowClassName='editable-row'
            pagination={{
              onChange: cancel,
            }}
          />
        </Form>
      </div>
    </>
  );
};

export default Products;
