import {
  Drawer,
  Form,
  Button,
  Col,
  Row,
  Input,
  Select,
  Typography,
} from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import React, { useState } from 'react';
import UploadImage from '../upload/UploadImage';
import UploadImageUpdate from '../upload/UploadImageUpdate';
import openNotification, {
  typeNotification,
} from '../../../components/openNotification';
import { UpdateProduct } from '../../../apis/shopAPIs';

const { Option } = Select;

const layout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 18 },
};

const ProductsFormUpdate = (props: any) => {
  const { record, refetch } = props;

  console.log(props, 'props');
  const {
    _id: id,
    name,
    price,
    active,
    count,
    imageUrl,
    voucherId,
    categoryId,
    describe,
    discount,
  } = record;

  const [isImageUrl, setIsImageUrl] = useState(imageUrl);

  const { Title } = Typography;

  const [visible, setVisible] = useState(false);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    form.resetFields();
    setVisible(false);
  };

  const [form] = Form.useForm();

  const onFinish = (values: any) => {
    console.log(values, 'value');
    const {
      name,
      price,
      count,
      active,
      voucherId,
      describe,
      categoryId,
      discount,
    } = values;

    UpdateProduct(
      id,
      name,
      parseFloat(price),
      isImageUrl ? isImageUrl : '',
      parseFloat(count),
      active === 'true' ? true : false,
      voucherId,
      describe,
      categoryId,
      parseFloat(discount)
    )
      .then((data: any) => {
        if (data.status === true) {
          refetch();
          openNotification(typeNotification.success, 'Update product success');
        } else {
          openNotification(typeNotification.error, 'Update product error');
        }
      })
      .catch((err: any) => {
        openNotification(typeNotification.error, err.toString());
      });
  };

  const handleUrlImage = (url: string) => {
    if (!url) {
      openNotification(typeNotification.error, 'Image error');
      return;
    }
    setIsImageUrl(url);
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log(errorInfo);
  };

  return (
    <>
      <a onClick={showDrawer}> Edit</a>
      <Drawer
        title='Update a Product'
        width={720}
        onClose={onClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
        footer={
          <div
            style={{
              textAlign: 'right',
            }}
          >
            <Button onClick={onClose} style={{ marginRight: 8 }}>
              Cancel
            </Button>
            <Button
              form='update-product'
              key='submit'
              htmlType='submit'
              type='primary'
            >
              Submit
            </Button>
          </div>
        }
      >
        <Form
          layout='vertical'
          name='basic'
          id='update-product'
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          className='em_form'
          form={form}
          // {...layout}
          initialValues={{
            name: name ? name : '',
            price: price ? price : '',
            active: active ? `${active}` : '',
            count: count ? count : '',
            imageUrl: imageUrl ? imageUrl : '',
            voucherId: voucherId ? `voucherId` : '',
            categoryId: categoryId ? `categoryId` : '',
            describe: describe ? describe : '',
            discount: discount ? discount : '',
          }}
        >
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item
                name='name'
                label='Name'
                rules={[{ required: true, message: 'Please enter user name' }]}
              >
                <Input placeholder='Please enter user name' />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name='price'
                label='Price'
                rules={[{ required: true, message: 'Please enter price' }]}
              >
                <Input type='number' placeholder='Please enter price' />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item
                name='count'
                label='Count'
                rules={[{ required: true, message: 'Please enter count' }]}
              >
                <Input type='number' placeholder='Please enter count' />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name='active'
                label='Active'
                rules={[
                  { required: true, message: 'Please choose the active' },
                ]}
              >
                <Select placeholder='Please choose the active'>
                  <Option value='false'>Private</Option>
                  <Option value='true'>Public</Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <Title level={5}>Upload Image</Title>
              <UploadImageUpdate
                handleUrlImage={handleUrlImage}
                imageUrl={imageUrl}
              />
            </Col>
            <Col span={12}>
              <Form.Item name='voucherId' label='Voucher'>
                <Select placeholder='Please choose the voucher'>
                  <Option value='voucherId'>{voucherId}</Option>
                  <Option value='Voucher1'>Voucher1</Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item
                name='discount'
                label='DisCount'
                rules={[{ required: true, message: 'Please enter discount' }]}
              >
                <Input type='number' placeholder='Please enter discount' />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name='categoryId'
                label='Category'
                rules={[
                  { required: true, message: 'Please choose the category' },
                ]}
              >
                <Select placeholder='Please choose the category'>
                  <Option value='categoryId'>{categoryId}</Option>
                  <Option value='category1'>Category</Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item name='describe' label='Describe'>
                <Input.TextArea rows={4} placeholder='please enter describe' />
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Drawer>
    </>
  );
};

export default ProductsFormUpdate;
