import React, { useState } from 'react';
import MenuNav from './menu/menu';
import { Redirect, Route, Router, Switch, useLocation } from 'react-router-dom';
import Users from './users/users';

import { Layout } from 'antd';
import { ROUTERS } from '../../router/ROUTERS';
import Products from './products/products';
import Category from './category/category';
import FeedBack from './feedback/feedback';
import Orders from './orders/orders';
import Voucher from './voucher/voucher';
import Dashboard from './dashboard/dashborad';

const { Content } = Layout;

const Admin = () => {
  const location = useLocation<any>();
  const [collapsed, setCollapsed] = useState(false);

  const toggleCollapsed = () => {
    setCollapsed(!collapsed);
  };

  return (
    <Layout>
      <MenuNav collapsed={collapsed} toggleCollapsed={toggleCollapsed} />
      <Content
        style={{
          overflow: 'initial',
          marginLeft: collapsed ? '60px' : '256px',
          textAlign: 'left',
          padding: '20px',
        }}
      >
        {location.pathname === ROUTERS.ADMIN &&
        !localStorage.getItem('token') ? (
          <Redirect to={`${ROUTERS.HOME}`} />
        ) : (
          ''
        )}
        {location.pathname === ROUTERS.ADMIN && (
          <Dashboard collapsed={collapsed} />
        )}
        <Switch>
          <Route path={ROUTERS.PRODUCTS} component={Products} />
          <Route path={ROUTERS.USERS} component={Users} />
          <Route path={ROUTERS.ORDERS} component={Orders} />
          <Route path={ROUTERS.CATEGORY} component={Category} />
          <Route path={ROUTERS.VOUCHER} component={Voucher} />
          <Route path={ROUTERS.FEEDBACK} component={FeedBack} />
        </Switch>
      </Content>
    </Layout>
  );
};

export default Admin;
