import {
  Drawer,
  Form,
  Button,
  Col,
  Row,
  Input,
  Select,
  Typography,
} from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import React, { useState } from 'react';
import UploadImage from '../upload/UploadImage';
import openNotification, {
  typeNotification,
} from '../../../components/openNotification';
import { AddProduct } from '../../../apis/shopAPIs';

const { Option } = Select;

const ProductsForm = (props: any) => {
  const { refetch } = props;
  const { Title } = Typography;

  const [visible, setVisible] = useState(false);
  const [imageUrl, setImageUrl] = useState('');

  const token: any = localStorage.getItem('token');

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  const urlImage = (url: string) => {
    if (!url) {
      openNotification(typeNotification.error, 'Image error');
      return;
    }
    setImageUrl(url);
  };

  const onFinish = (values: any) => {
    const {
      name,
      price,
      count,
      active,
      voucher: voucherId,
      description: describe,
      category: categoryId,
      discount,
    } = values;

    AddProduct(
      token,
      name,
      parseFloat(price),
      imageUrl ? imageUrl : '',
      parseFloat(count),
      active === 'true' ? true : false,
      voucherId,
      describe,
      categoryId,
      parseFloat(discount)
    )
      .then((data: any) => {
        console.log(data);

        if (data.status === true) {
          refetch();
          openNotification(typeNotification.success, 'Add product success');
        } else {
          openNotification(typeNotification.error, 'Add product error');
        }
      })
      .catch((err: any) => {
        openNotification(typeNotification.error, err);
      });
  };

  return (
    <>
      <Button type='primary' onClick={showDrawer}>
        <PlusOutlined /> Add Product
      </Button>
      <Drawer
        title='Create a Product'
        width={720}
        onClose={onClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
        footer={
          <div
            style={{
              textAlign: 'right',
            }}
          >
            <Button onClick={onClose} style={{ marginRight: 8 }}>
              Cancel
            </Button>
            <Button
              // onClick={onClose}
              type='primary'
              htmlType='submit'
              form='add-product'
              key='submit'
            >
              Submit
            </Button>
          </div>
        }
      >
        <Form
          layout='vertical'
          id='add-product'
          hideRequiredMark
          onFinish={onFinish}
        >
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item
                name='name'
                label='Name'
                rules={[{ required: true, message: 'Please enter user name' }]}
              >
                <Input placeholder='Please enter user name' />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name='price'
                label='Price'
                rules={[{ required: true, message: 'Please enter price' }]}
              >
                <Input type='number' placeholder='Please enter price' />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item
                name='count'
                label='Count'
                rules={[{ required: true, message: 'Please enter count' }]}
              >
                <Input type='number' placeholder='Please enter count' />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name='active'
                label='Active'
                rules={[
                  { required: true, message: 'Please choose the active' },
                ]}
              >
                <Select placeholder='Please choose the active'>
                  <Option value='false'>Private</Option>
                  <Option value='true'>Public</Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <Title level={5}>Upload Image</Title>
              <UploadImage urlImage={urlImage} />
            </Col>
            <Col span={12}>
              <Form.Item name='voucher' label='Voucher'>
                <Select placeholder='Please choose the voucher'>
                  <Option value='Voucher'>Voucher</Option>
                  <Option value='Voucher1'>Voucher1</Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item
                name='discount'
                label='DisCount'
                rules={[{ required: true, message: 'Please enter disCount' }]}
              >
                <Input type='number' placeholder='Please enter Count' />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name='category'
                label='Category'
                rules={[
                  { required: true, message: 'Please choose the category' },
                ]}
              >
                <Select placeholder='Please choose the category'>
                  <Option value='category'>Category</Option>
                  <Option value='category1'>Category</Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item name='description' label='Description'>
                <Input.TextArea
                  rows={4}
                  placeholder='please enter description'
                />
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Drawer>
    </>
  );
};

export default ProductsForm;
