import { Modal, Button } from "antd";
import React, { useState } from "react";
import { Form, Input, Checkbox } from "antd";
import { UserOutlined, LockOutlined } from "@ant-design/icons";
import { useHistory } from "react-router-dom";
import { ROUTERS } from "../../../../router/ROUTERS";
import { Login } from "../../../../apis/shopAPIs";
import openNotification, {
  typeNotification,
} from "../../../../components/openNotification";

interface props {
  showModal: () => any;
  closeModal: () => any;
  isOpenModal: boolean;
}

function SignIn(props: any) {
  const history = useHistory();

  const [isAuthenticated, makeAuthenticated] = React.useState(false);

  const [userName, setUserName] = useState();
  const [passWord, setPassWord] = useState();

  const handleChangeUserName = (e: any) => {
    const value = e.target.value;
    setUserName(value);
  };

  const handleChangePassword = (e: any) => {
    const value = e.target.value;
    setPassWord(value);
  };

  const onFinish = (values: any) => {
    Login(values.username, values.password)
      .then((data: any) => {
        if (data.status) {
          localStorage.setItem("token", data.token);
          makeAuthenticated(true);
          if (
            values.username === "adminshop@gmail.com" &&
            values.password === "adminshop123"
          ) {
            history.push(ROUTERS.ADMIN);
          } else {
            history.push(ROUTERS.HOME);
          }
          props.closeModal();
        }
      })
      .catch((err) => {
        openNotification(typeNotification.error, err);
      });
  };

  const signOut = () => {
    makeAuthenticated(false);
    localStorage.removeItem("token");
  };

  return !isAuthenticated ? (
    <>
      <Button type="link" onClick={props.showModal}>
        Sign in
      </Button>
      <Modal
        title="Sign in"
        visible={props.isOpenModal}
        // onOk={props.showModal}
        onOk={() => history.push(ROUTERS.LOGIN)}
        onCancel={props.closeModal}
        footer={[]}
      >
        <Form
          name="normal_login"
          className="login-form"
          initialValues={{ remember: true }}
          onFinish={onFinish}
        >
          <Form.Item
            name="username"
            rules={[{ required: true, message: "Please input your Username!" }]}
          >
            <Input
              prefix={<UserOutlined className="site-form-item-icon" />}
              placeholder="Username"
              onChange={handleChangeUserName}
            />
          </Form.Item>
          <Form.Item
            name="password"
            rules={[{ required: true, message: "Please input your Password!" }]}
          >
            <Input
              prefix={<LockOutlined className="site-form-item-icon" />}
              type="password"
              placeholder="Password"
              onChange={handleChangePassword}
            />
          </Form.Item>
          <Form.Item>
            <Form.Item name="remember" valuePropName="checked" noStyle>
              <Checkbox>Remember me</Checkbox>
            </Form.Item>

            <a href="#" className="login-form-forgot">
              Forgot password
            </a>
          </Form.Item>

          <Form.Item>
            <Button
              type="primary"
              htmlType="submit"
              className="login-form-button"
            >
              Log in
            </Button>
            Or <a href="#"> register now!</a>
          </Form.Item>
        </Form>
      </Modal>
    </>
  ) : (
    <Button type="link" onClick={signOut}>
      Sign Out
    </Button>
  );
}
export default SignIn;
