import { Drawer, Form, Button, Col, Row, Input, Select, Typography } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import React, { useState } from 'react';
import UploadImage from '../upload/UploadImage';

const { Option } = Select;

const UsersForm = () => {

    const { Title } = Typography;

    const [visible, setVisible] = useState(false);

    const showDrawer = () => {
        setVisible(true);
    };

    const onClose = () => {
        setVisible(false);
    };

    return (
        <>
            <Button type="primary" onClick={showDrawer}>
                <PlusOutlined /> Add User
            </Button>
            <Drawer
                title="Create a User"
                width={720}
                onClose={onClose}
                visible={visible}
                bodyStyle={{ paddingBottom: 80 }}
                footer={
                    <div
                        style={{
                            textAlign: 'right',
                        }}
                    >
                        <Button onClick={onClose} style={{ marginRight: 8 }}>
                            Cancel
              </Button>
                        <Button onClick={onClose} type="primary">
                            Submit
              </Button>
                    </div>
                }
            >
                <Form layout="vertical" hideRequiredMark>
                    <Row gutter={16} style={{ paddingBottom: 5 }}>
                        <Col >
                            <Title level={5}>Upload Image</Title>
                            <UploadImage />
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col span={12}>
                            <Form.Item
                                name="name"
                                label="Name"
                                rules={[{ required: true, message: 'Please enter user name' }]}
                            >
                                <Input placeholder="Please enter user name" />
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item
                                name="price"
                                label="Price"
                                rules={[{ required: true, message: 'Please enter price' }]}
                            >
                                <Input placeholder="Please enter price" />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col span={12}>
                            <Form.Item
                                name="count"
                                label="Count"
                                rules={[{ required: true, message: 'Please enter count' }]}
                            >
                                <Input placeholder="Please enter count" />
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item
                                name="active"
                                label="Active"
                                rules={[{ required: true, message: 'Please choose the active' }]}
                            >
                                <Select placeholder="Please choose the active">
                                    <Option value="private">Private</Option>
                                    <Option value="public">Public</Option>
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col span={12}>
                            <Form.Item
                                name="voucher"
                                label="Voucher"
                                rules={[{ required: true, message: 'Please choose the voucher' }]}
                            >
                                <Select placeholder="Please choose the voucher">
                                    <Option value="Voucher">Voucher</Option>
                                    <Option value="Voucher1">Voucher1</Option>
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item
                                name="category"
                                label="Category"
                                rules={[{ required: true, message: 'Please choose the category' }]}
                            >
                                <Select placeholder="Please choose the category">
                                    <Option value="category">Category</Option>
                                    <Option value="category1">Category</Option>
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col span={24}>
                            <Form.Item
                                name="description"
                                label="Description"
                                rules={[
                                    {
                                        required: true,
                                        message: 'please enter url description',
                                    },
                                ]}
                            >
                                <Input.TextArea rows={4} placeholder="please enter url description" />
                            </Form.Item>
                        </Col>
                    </Row>
                </Form>
            </Drawer>
        </>
    );
}


export default UsersForm;