import { PageHeader } from "antd";
import React from "react";

function PageHeaderComp(props: any) {
  const { title } = props;

  return (
    <div style={{ marginBottom: "20px" }}>
      <PageHeader
        ghost={false}
        onBack={() => window.history.back()}
        title={title}
      ></PageHeader>
    </div>
  );
}

export default PageHeaderComp;
